import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Row, Col } from 'antd';
import InfoCountTotalCard from '../../../components/common/InfoCard/index';
import {
  getQuickLinkEnrollmentGetAreaNo,
  getQuickLinkEnrollmentGetEnrollSolutionNo,
  getQuickLinkEnrollmentGetCandidateNo,
  getQuickLinkEnrollmentGetCandidateEnrolledNo,
} from '../../../services/quicklink';

const COLORS = [
  '#0088FE',
  '#00C49F',
  '#FFBB28',
  '#FF8042',
  '#0088fed9',
  '#00c49fd9',
  '#ffbb28d9',
  '#ff8042d9',
];

const data = [
  {
    title: 'Khu vực tuyển sinh',
    service: getQuickLinkEnrollmentGetAreaNo,
    url: '/zones/list',
  },
  {
    title: 'Phương án tuyển sinh',
    service: getQuickLinkEnrollmentGetEnrollSolutionNo,
    url: '/enrollment-plan/list',
  },
  {
    title: 'Ứng viên',
    service: getQuickLinkEnrollmentGetCandidateNo,
    url: '/students/candidate/list',
  },
  {
    title: 'Ứng viên đã nhập học',
    service: getQuickLinkEnrollmentGetCandidateEnrolledNo,
    url: '/admission-papers/receive',
  },
];

class DashboardEnroll extends Component {
  state = {};
  render() {
    return (
      <Row gutter={24}>
        {data.map((item, index) => (
          <Col span={6} key={item.title}>
            <InfoCountTotalCard
              backgroundColor={COLORS[index % 8]}
              title={item.title}
              service={item.service}
              icon={item.icon}
              url={item.url}
            />
          </Col>
        ))}
      </Row>
    );
  }
}

export default props => (
  <Page {...props}>
    <DashboardEnroll {...props} />
  </Page>
);



